.App {
  display: grid;
  /*   justify-content: center; */
  align-self: start;
  text-align: center;
  font-size: 24px;
  color: white;

  /*   text-shadow: 2px 1px 2px rgba(0, 0, 0, 1); */

  box-sizing: border-box;
  margin: 0;
  padding: 0;

  min-height: 100vh;
  width: 100vw;
  max-width: 100%;

  background-image: linear-gradient(
    to right,
    rgb(71, 38, 13),
    rgb(15, 90, 100)
  );
}

a {
  color: rgb(16, 229, 198, 1);
}

img {
  max-width: 100%;
  height: auto;
}

.BtnPrimary {
  padding: 22px 44px;
  color: white;
  background: rgb(216, 108, 11);
}

.BtnPrimary:hover {
  box-shadow: inset 0 0 0 10em rgba(255, 255, 255, 0.3);
}

.BtnSecondary {
  box-sizing: border-box;
  padding: 10px 20px;
  color: white;
  background: rgb(0, 35, 66);
  border: 1px rgba(0, 0, 0, 0) solid;
}

.BtnSecondary:hover {
  color: rgb(0, 185, 151);
  border: 1px rgb(0, 185, 151) solid;
}

.BtnTertiary {
  box-sizing: border-box;
  padding: 10px 0px;
  margin-left: 40px;
  color: white;
  background: none;
}

.BtnTertiary:hover {
  color: rgb(0, 185, 151);
}

.BgGradienteVerde {
  background-image: url("images/gradiente\ verde.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.ProgressBarBox {
  text-align: left;
  text-indent: 1.5rem;
}

.ProgressBarBox > * {
  padding: 5px 0;
}

.UtilityTab {
  font-size: 1.2rem;
}

.TokenUtilityList {
  /*font-size: 100%;*/
  text-align: left;
  margin: auto auto auto 0;
  padding: 18px 0 60px 0;
}

#TokenUtilityTabs img {
  max-height: 220px;
  margin: 20px auto;
  justify-self: center;
}

#topMenuBg {
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
}

#topMenuContainer {
  padding: 60px 20px 40px 20px;
}

#topMenuLogo {
  display: inline-flex;
  margin: 0 auto 40px auto;
}

#topMenuLogo > img {
  height: 180px;
}

#topMenuMainButtons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

#topMenuMainButtons a {
  margin-left: 6px;
  margin-bottom: 12px;
}

#roadmapContainer ul {
  margin: 0;
  padding-inline-start: 10px;
}

#partnersRow > * {
  padding-bottom: 40px;
}

#partnersRow img {
  margin: auto;
}

#bottomMenu {
  background: black;
}

#bottomMenu > :first-child {
  padding: 90px 25px 100px 25px;
}

#bottomMenuLogo {
  margin: 0 auto 40px auto;
}

#bottomMenuLogo > img {
  height: 150px;
}

#bottomMenu #topMenuMainButtons a {
  margin-left: 30px;
}

#bottomMenuSocialButtons {
  display: flex;
  justify-content: end;
  flex-wrap: wrap;
}

#bottomMenuSocialButtons a {
  margin-left: 10px;
}

#welcomeSection img {
  /*
  background: url("images/char1-faded.png") center top no-repeat;
  background-size: contain;
  */
  max-height: 800px;
}

#Welcome {
  line-height: 2.15em;
  padding-top: 0px;
  padding-bottom: 80px;
}

#DownloadButtonsBox {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
}

#presaleContainer {
  position: relative;
  min-height: 500px;
  max-width: 100%;
}

#presaleBg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("images/dragon\ fight\ 70.png") 35% 10% no-repeat;
  background-size: cover;
  opacity: 0.7;
  filter: blur(4px);
}

#presaleContent {
  z-index: 2;
}

/*** MEDIA QUERY ***/
/**********************/

@media screen and (min-width: 668px) {
  #topMenuLogo > img {
    margin: 0 auto auto 0;
  }

  #topMenuMainButtons {
    justify-content: end;
  }

  #presaleContainer {
    background-position: center bottom;
  }
}
